button {
  cursor: pointer;
  border: 0 #000;
  border-radius: 8px;
  margin-top: 250px;
  padding: 10px 40px;
  transition: all .3s;
  box-shadow: 0 0 10px 1px rgba(0, 0, 0, .75);
}

button:hover {
  transform: scale(.95);
}

body {
  justify-content: center;
  gap: 20px;
  transition: all .3s;
  display: flex;
}

